.expenseItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: black;
}

.expenseDesc {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1;
}

.expenseItem h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
}

.expensePrice {
  width: 7em;
  font-size: 1rem;
  font-weight: bold;
  text-align: center;
  color: white;
  background-color: royalblue;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
}

.button {
  position: relative;
  top: -35px;
  width: 1.5em;
  height: 1.5em;
  font-size: 0.9rem;
  font-weight: bold;
  color: white;
  background: transparent;
  border-color: transparent;
}

.button:hover {
  transform: scale(1.3);
}

@media (min-width: 768px) {
  .expenseDesc {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
  }

  .expenseDesc h2 {
    font-size: 1.25rem;
  }

  .expensePrice {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}
